<template>
  <v-dialog v-model="isOpenDelete" persistent max-width="290">
    <v-card>
      <v-card-title class="headline"> Ests seguro? </v-card-title>
      <v-card-text>
        La tarjeta {{ item.cardId }} ser eliminada de manera permanente.
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" text @click="closeDialogHandler(false)">
          Cancelar
        </v-btn>
        <v-btn color="green darken-1" text @click="deleteDialogHandler(false)">
          Borrar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "DeleteDialog",
  props: {
    item: {
      type: Object,
      default: () => {
        return {
          id: "",
          cardId: "",
        };
      },
    },
  },
  data: () => ({}),
  methods: {
    ...mapMutations(["changeDeleteDialog"]),
    closeDialogHandler(dialog) {
      this.item.id = "";
      this.item.cardId = "";
      this.changeDeleteDialog({ dialog });
    },
    deleteDialogHandler(dialog) {
      this.$store.dispatch("deleteCard", { id: this.item.id }).finally(() => {
        this.changeDeleteDialog({ dialog });
      });
    },
  },
  computed: {
    ...mapGetters(["isOpenDelete"]),
  },
};
</script>

