<template>
  <v-card>
    <v-card-title>
      <v-row no-gutters>
        <v-col cols="2">
          <v-row justify="center">
            <v-btn
              color="#c98b3e"
              class="white--text"
              fab
              small
              bottom
              @click="openDialog"
            >
              <v-icon>mdi-plus</v-icon>
            </v-btn>
          </v-row>
        </v-col>
        <v-col cols="8"> Tarjetas de pre-pago </v-col>
      </v-row>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        :label="searchLabel"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table 
      :headers="headers" 
      :items="cardList" 
      :search="search"
      :loading="cardList.length == 0"
      loading-text="Cargando...Porfavor espere"
    >
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>
    </v-data-table>
    <CreateDialog></CreateDialog>
    <DeleteDialog :item="itemToDelete"></DeleteDialog>
  </v-card>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import CreateDialog from "@/components/CreateDialog.vue";
import DeleteDialog from "@/components/DeleteDialog.vue";

export default {
  name: "Home",
  components: {
    CreateDialog,
    DeleteDialog,
  },
  data: () => ({
    searchLabel: "Coloque la tarjeta en el lector...",
    search: "",
    itemToDelete: {
      id: "",
      cardId: "",
    },
    headers: [
      {
        text: "Id",
        align: "start",
        sortable: false,
        value: "id",
      },
      { text: "Nmero", value: "cardId" },
      { text: "Paquete", value: "pack" },
      { text: "Disponibles", value: "available" },
      { text: "Fecha", value: "createdAt" },
      { text: "Opciones", value: "actions" },
    ]
  }),
  mounted(){
    this.$store.dispatch('getCards');
  },
  methods: {
    ...mapMutations(["changeCreateDialog", "changeDeleteDialog"]),
    deleteItem({ id, cardId }) {
      this.itemToDelete.id = id;
      this.itemToDelete.cardId = cardId;
      this.changeDeleteDialog({ dialog: true });
    },
    openDialog() {
      this.changeCreateDialog({ dialog: true });
    },
  },
  computed: {
    ...mapGetters(["cardList"]),
  },
};
</script>
